body {
    background: #F3F2F5;
}

.page-title {
    text-transform: uppercase;
    padding-bottom: 30px;
}

.page-title span {
    color: #788687;
}

.result-block {
    grid-template-columns: 1fr 2fr;
    grid-gap: 20px;
}

.search-result {
    background: #fff;
    margin-bottom: 25px;
    padding: 16px 18px 16px 18px;
    font-size: 13px;
    font-weight: 600;
    text-transform: uppercase;
    box-shadow: 0 1px 3px 0 rgba(37,32,31,.3);
}

.search-result span {
    display: block;
    height: 15px;
    background: url("img/result-icon.png") no-repeat left 2px;
    padding: 0 0 0 28px;
}

.result-catalog__filter {
    background: #fff;
    padding: 5px 17px 7px 17px;
    margin-bottom: 25px;
    box-shadow: 0 1px 3px 0 rgba(37,32,31,.3);

}

.result-catalog__filter label {
    margin: 9px 15px 0 0;
    font-weight: 600;
    text-transform: uppercase;
    font-size: 11px;
}

.search-select {
    padding: 9px 0 8px 0;
    font-size: 12px;
    text-transform: uppercase;
    color: #8a8a8a;
    background: url(img/select-bg.png) no-repeat 80% center;
    display: inline-block;
    width: 89px;
    margin: 1px 10px 0 0 ;
    border: 1px solid #dedcdc;
    cursor: pointer;
}


.price-select {
    padding-left: 5px;
}

.side-block {
    box-shadow: 0 1px 3px 0 rgba(37,32,31,.3);
    background: #ffffff;
}

.side-block__search {
    padding: 22px 18px 22px 18px;
    grid-template-columns: 1fr 1fr;
    text-align: center;
}

.side-block__search label {
    display: block;
    text-align: left;
    font-size: 11px;
    color: #4c4c4c;
    text-transform: uppercase;
    font-weight: normal;
    margin: 20px 0 4px 8px;
}

.side-block__search input {
    width: 80%;
    padding: 6px 7px 7px 7px;
    background: #fff none repeat scroll 0 0;
    border: 1px solid #e3e3e3;
    border-radius: 3px;
    box-shadow: 0 1px 1px 0 rgba(50, 50, 50, 0.05);
    color: #8a8a8a;
}

.side-block__button button {
    display: block;
    width: 102px;
    height: 36px;
    background: #f76962;
    border-radius: 2px;
    text-align: center;
    font-size: 11px;
    text-decoration: none;
    color: #fff;
    border: none;
    margin: 20px 0 15px 8px;
    text-transform: uppercase;
    cursor: pointer;
}

.slider-price {
    box-shadow: 0 1px 3px 0 rgba(37,32,31,.3);
    margin-top: 25px;
    background: #ffffff;
    padding: 21px 18px 20px 18px;
}

.slider-price .slider-title {
    font-size: 13px;
    margin-bottom: 16px;
    color: #3a4142;
    text-transform: uppercase;
}

.price-amounts input {
    width: 100px;
    font-size: 12px;
    color: #666666;
    border: 0;
    background: #fff;
}

.price-amounts input:last-child {
    float: right;
    text-align: right;
}

.rc-slider-track {
    background: #4a90a4;
}

.rc-slider-handle {
    height: 18px;
    width: 18px;
    margin-top: -7px;
    border: 1px solid #c2c5c8;
}

.side-stops {
    box-shadow: 0 1px 3px 0 rgba(37,32,31,.3);
    padding: 21px 18px 20px 18px;
    background: #ffffff;
    margin-bottom: 25px;
}

.side-title {
    font-size: 13px;
    margin-bottom: 16px;
    color: #3a4142;
    text-transform: uppercase;;
}

.radio-label{
    cursor: pointer;
    font-weight: 600;
    text-transform: uppercase;
    font-size: 12px;
    color: #848484;
}


.radio-area {
    margin-bottom: 10px;
}

.radio-area input {
    position: absolute;
    left: -9999px;
}

.radio-area .checkbox-span {
    display: inline-block;
    width: 15px;
    margin: 0 10px 0 0;
    background: #fff;
    box-shadow: none;
    border: 1px solid #dedcdc;
    border-radius: 2px;
    height: 15px;
    position: relative;
}

.radio-area input[type=radio]:checked + span {
    background: url(img/checkbox.png) center no-repeat;;
}
.reverse-icon {
    height: 18px;
    width: 18px;
    margin: 0px 10px 0px 10px;
}